const handleEdit = (item) => {
  setEditingItem({ ...item });
};
import React, { useState, useEffect } from "react";
import { Plus, Edit, Trash2, Save, X, Search, RefreshCw } from "lucide-react";

const MasterDataInterface = () => {
  const [activeTab, setActiveTab] = useState("capacity");
  const [capacityData, setCapacityData] = useState([]);
  const [monthData, setMonthData] = useState([]);
  const [weekData, setWeekData] = useState([]);
  const [scheduleData, setScheduleData] = useState([]);
  const [editingItem, setEditingItem] = useState(null);
  const [showAddForm, setShowAddForm] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");
  const [loading, setLoading] = useState(false);

  // API endpoints
  const APIs = {
    capacity: "http://localhost:8080/api/v1/collection/kln_master_data",
    month: "http://localhost:8080/api/v1/collection/kln_ppc_month",
    week: "http://localhost:8080/api/v1/collection/kln_ppc_week",
    schedule: "https://ktflceprd.kalyanicorp.com/internal/kln_prod_schedule",
  };

  // Fetch data from APIs
  const fetchData = async () => {
    setLoading(true);
    try {
      // Fetch all data concurrently
      const [capacityRes, monthRes, weekRes, scheduleRes] =
        await Promise.allSettled([
          fetch(APIs.capacity),
          fetch(APIs.month),
          fetch(APIs.week),
          fetch(APIs.schedule),
        ]);

      // Handle capacity data
      if (capacityRes.status === "fulfilled" && capacityRes.value.ok) {
        const capacityJson = await capacityRes.value.json();
        setCapacityData(capacityJson.objects || []);
      } else {
        console.error("Failed to fetch capacity data:", capacityRes.reason);
        setCapacityData([]);
      }

      // Handle month data
      if (monthRes.status === "fulfilled" && monthRes.value.ok) {
        const monthJson = await monthRes.value.json();
        setMonthData(monthJson.objects || []);
      } else {
        console.error("Failed to fetch month data:", monthRes.reason);
        setMonthData([]);
      }

      // Handle week data
      if (weekRes.status === "fulfilled" && weekRes.value.ok) {
        const weekJson = await weekRes.value.json();
        setWeekData(weekJson.objects || []);
      } else {
        console.error("Failed to fetch week data:", weekRes.reason);
        setWeekData([]);
      }

      // Handle schedule data
      if (scheduleRes.status === "fulfilled" && scheduleRes.value.ok) {
        const scheduleJson = await scheduleRes.value.json();
        setScheduleData(scheduleJson.data || []);
      } else {
        console.error("Failed to fetch schedule data:", scheduleRes.reason);
        setScheduleData([]);
      }
    } catch (error) {
      console.error("Error fetching data:", error);
    } finally {
      setLoading(false);
    }
  };

  // Initial data fetch
  useEffect(() => {
    fetchData();
  }, []);

  // CRUD Operations with API calls
  const handleAdd = async (data) => {
    setLoading(true);
    try {
      const endpoint = APIs[activeTab];
      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });

      if (response.ok) {
        const newItem = await response.json();

        switch (activeTab) {
          case "capacity":
            setCapacityData((prev) => [...prev, newItem]);
            break;
          case "month":
            setMonthData((prev) => [...prev, newItem]);
            break;
          case "week":
            setWeekData((prev) => [...prev, newItem]);
            break;
          case "schedule":
            setScheduleData((prev) => [...prev, newItem]);
            break;
        }
        setShowAddForm(false);
        alert("Data added successfully!");
      } else {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
    } catch (error) {
      console.error("Error adding data:", error);
      alert("Failed to add data. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async (updatedItem) => {
    setLoading(true);
    try {
      const endpoint = APIs[activeTab];
      const itemId = updatedItem.cdb_object_id || updatedItem.order_id;
      const response = await fetch(`${endpoint}/${itemId}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(updatedItem),
      });

      if (response.ok) {
        const updateData = (data) =>
          data.map((item) =>
            item.cdb_object_id === itemId || item.order_id === itemId
              ? updatedItem
              : item
          );

        switch (activeTab) {
          case "capacity":
            setCapacityData(updateData);
            break;
          case "month":
            setMonthData(updateData);
            break;
          case "week":
            setWeekData(updateData);
            break;
          case "schedule":
            setScheduleData(updateData);
            break;
        }
        setEditingItem(null);
        alert("Data updated successfully!");
      } else {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
    } catch (error) {
      console.error("Error updating data:", error);
      alert("Failed to update data. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    if (!confirm("Are you sure you want to delete this item?")) return;

    setLoading(true);
    try {
      const endpoint = APIs[activeTab];
      const response = await fetch(`${endpoint}/${id}`, {
        method: "DELETE",
      });

      if (response.ok) {
        const filterData = (data) =>
          data.filter(
            (item) => item.cdb_object_id !== id && item.order_id !== id
          );

        switch (activeTab) {
          case "capacity":
            setCapacityData(filterData);
            break;
          case "month":
            setMonthData(filterData);
            break;
          case "week":
            setWeekData(filterData);
            break;
          case "schedule":
            setScheduleData(filterData);
            break;
        }
        alert("Data deleted successfully!");
      } else {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
    } catch (error) {
      console.error("Error deleting data:", error);
      alert("Failed to delete data. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const getCurrentData = () => {
    switch (activeTab) {
      case "capacity":
        return capacityData;
      case "month":
        return monthData;
      case "week":
        return weekData;
      case "schedule":
        return scheduleData;
      default:
        return [];
    }
  };

  const filteredData = getCurrentData().filter((item) =>
    Object.values(item).some(
      (value) =>
        value &&
        value.toString().toLowerCase().includes(searchTerm.toLowerCase())
    )
  );

  // Form Components
  const CapacityForm = ({ data, onSave, onCancel }) => {
    const [formData, setFormData] = useState(
      data || {
        die_no: "",
        ideal_cap: "",
        ideal_cycle_time: "",
        ideal_days_booked: "",
        ideal_schedule: "",
        machine_name: "",
        machine_operation: "",
        part_name: "",
        plant_code: "",
        work_center: "",
      }
    );

    return (
      <div style={styles.formContainer}>
        <h3 style={styles.formTitle}>
          {data ? "Edit Capacity Master" : "Add Capacity Master"}
        </h3>
        <div style={styles.formGrid}>
          <input
            style={styles.input}
            placeholder="Die No"
            value={formData.die_no || ""}
            onChange={(e) =>
              setFormData({ ...formData, die_no: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Ideal Capacity"
            type="number"
            value={formData.ideal_cap || ""}
            onChange={(e) =>
              setFormData({ ...formData, ideal_cap: parseInt(e.target.value) })
            }
          />
          <input
            style={styles.input}
            placeholder="Ideal Cycle Time"
            type="number"
            value={formData.ideal_cycle_time || ""}
            onChange={(e) =>
              setFormData({
                ...formData,
                ideal_cycle_time: parseInt(e.target.value),
              })
            }
          />
          <input
            style={styles.input}
            placeholder="Ideal Days Booked"
            type="number"
            value={formData.ideal_days_booked || ""}
            onChange={(e) =>
              setFormData({
                ...formData,
                ideal_days_booked: parseInt(e.target.value),
              })
            }
          />
          <input
            style={styles.input}
            placeholder="Machine Name"
            value={formData.machine_name || ""}
            onChange={(e) =>
              setFormData({ ...formData, machine_name: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Machine Operation"
            value={formData.machine_operation || ""}
            onChange={(e) =>
              setFormData({ ...formData, machine_operation: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Part Name"
            value={formData.part_name || ""}
            onChange={(e) =>
              setFormData({ ...formData, part_name: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Plant Code"
            value={formData.plant_code || ""}
            onChange={(e) =>
              setFormData({ ...formData, plant_code: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Work Center"
            value={formData.work_center || ""}
            onChange={(e) =>
              setFormData({ ...formData, work_center: e.target.value })
            }
          />
        </div>
        <div style={styles.formActions}>
          <button
            style={{
              ...styles.button,
              ...styles.saveButton,
              opacity: loading ? 0.6 : 1,
              cursor: loading ? "not-allowed" : "pointer",
            }}
            onClick={() => onSave(formData)}
            disabled={loading}
          >
            <Save size={16} /> {loading ? "Saving..." : "Save"}
          </button>
          <button
            style={{
              ...styles.button,
              ...styles.cancelButton,
              opacity: loading ? 0.6 : 1,
              cursor: loading ? "not-allowed" : "pointer",
            }}
            onClick={onCancel}
            disabled={loading}
          >
            <X size={16} /> Cancel
          </button>
        </div>
      </div>
    );
  };

  const MonthForm = ({ data, onSave, onCancel }) => {
    const [formData, setFormData] = useState(
      data || {
        customer_name: "",
        mapped_prod_no: "",
        plant_code: "",
        month_die_no: "",
        month_forge_press: "",
        month_gross_wt: "",
        month_net_tonn: "",
        month_prod_date: "",
        month_sale_plan_qty: "",
        month_section: "",
      }
    );

    return (
      <div style={styles.formContainer}>
        <h3 style={styles.formTitle}>
          {data ? "Edit Monthly Plan" : "Add Monthly Plan"}
        </h3>
        <div style={styles.formGrid}>
          <input
            style={styles.input}
            placeholder="Customer Name"
            value={formData.customer_name || ""}
            onChange={(e) =>
              setFormData({ ...formData, customer_name: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Product No"
            value={formData.mapped_prod_no || ""}
            onChange={(e) =>
              setFormData({ ...formData, mapped_prod_no: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Plant Code"
            type="number"
            value={formData.plant_code || ""}
            onChange={(e) =>
              setFormData({ ...formData, plant_code: parseInt(e.target.value) })
            }
          />
          <input
            style={styles.input}
            placeholder="Die No"
            value={formData.month_die_no || ""}
            onChange={(e) =>
              setFormData({ ...formData, month_die_no: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Forge Press"
            value={formData.month_forge_press || ""}
            onChange={(e) =>
              setFormData({ ...formData, month_forge_press: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Production Date"
            type="date"
            value={formData.month_prod_date || ""}
            onChange={(e) =>
              setFormData({ ...formData, month_prod_date: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Sale Plan Qty"
            type="number"
            value={formData.month_sale_plan_qty || ""}
            onChange={(e) =>
              setFormData({
                ...formData,
                month_sale_plan_qty: parseInt(e.target.value),
              })
            }
          />
          <input
            style={styles.input}
            placeholder="Section"
            type="number"
            value={formData.month_section || ""}
            onChange={(e) =>
              setFormData({
                ...formData,
                month_section: parseInt(e.target.value),
              })
            }
          />
        </div>
        <div style={styles.formActions}>
          <button
            style={{ ...styles.button, ...styles.saveButton }}
            onClick={() => onSave(formData)}
          >
            <Save size={16} /> Save
          </button>
          <button
            style={{ ...styles.button, ...styles.cancelButton }}
            onClick={onCancel}
          >
            <X size={16} /> Cancel
          </button>
        </div>
      </div>
    );
  };

  const WeekForm = ({ data, onSave, onCancel }) => {
    const [formData, setFormData] = useState(
      data || {
        customer_name: "",
        mapped_month_prod_no: "",
        planned_qty: "",
        plant_code: "",
        shift1_qty: "",
        shift2_qty: "",
        shift3_qty: "",
        week_prod_date: "",
        week_die_no: "",
        main_forge_press: "",
      }
    );

    return (
      <div style={styles.formContainer}>
        <h3 style={styles.formTitle}>
          {data ? "Edit Weekly Plan" : "Add Weekly Plan"}
        </h3>
        <div style={styles.formGrid}>
          <input
            style={styles.input}
            placeholder="Customer Name"
            value={formData.customer_name || ""}
            onChange={(e) =>
              setFormData({ ...formData, customer_name: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Product No"
            value={formData.mapped_month_prod_no || ""}
            onChange={(e) =>
              setFormData({ ...formData, mapped_month_prod_no: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Planned Qty"
            type="number"
            value={formData.planned_qty || ""}
            onChange={(e) =>
              setFormData({
                ...formData,
                planned_qty: parseInt(e.target.value),
              })
            }
          />
          <input
            style={styles.input}
            placeholder="Plant Code"
            type="number"
            value={formData.plant_code || ""}
            onChange={(e) =>
              setFormData({ ...formData, plant_code: parseInt(e.target.value) })
            }
          />
          <input
            style={styles.input}
            placeholder="Shift 1 Qty"
            type="number"
            value={formData.shift1_qty || ""}
            onChange={(e) =>
              setFormData({ ...formData, shift1_qty: parseInt(e.target.value) })
            }
          />
          <input
            style={styles.input}
            placeholder="Shift 2 Qty"
            type="number"
            value={formData.shift2_qty || ""}
            onChange={(e) =>
              setFormData({ ...formData, shift2_qty: parseInt(e.target.value) })
            }
          />
          <input
            style={styles.input}
            placeholder="Shift 3 Qty"
            type="number"
            value={formData.shift3_qty || ""}
            onChange={(e) =>
              setFormData({ ...formData, shift3_qty: parseInt(e.target.value) })
            }
          />
          <input
            style={styles.input}
            placeholder="Production Date"
            type="date"
            value={formData.week_prod_date || ""}
            onChange={(e) =>
              setFormData({ ...formData, week_prod_date: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Die No"
            value={formData.week_die_no || ""}
            onChange={(e) =>
              setFormData({ ...formData, week_die_no: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Forge Press"
            value={formData.main_forge_press || ""}
            onChange={(e) =>
              setFormData({ ...formData, main_forge_press: e.target.value })
            }
          />
        </div>
        <div style={styles.formActions}>
          <button
            style={{ ...styles.button, ...styles.saveButton }}
            onClick={() => onSave(formData)}
          >
            <Save size={16} /> Save
          </button>
          <button
            style={{ ...styles.button, ...styles.cancelButton }}
            onClick={onCancel}
          >
            <X size={16} /> Cancel
          </button>
        </div>
      </div>
    );
  };

  const ScheduleForm = ({ data, onSave, onCancel }) => {
    const [formData, setFormData] = useState(
      data || {
        order_id: "",
        customer: "",
        amount: "",
      }
    );

    return (
      <div style={styles.formContainer}>
        <h3 style={styles.formTitle}>
          {data ? "Edit Production Schedule" : "Add Production Schedule"}
        </h3>
        <div style={styles.formGrid}>
          <input
            style={styles.input}
            placeholder="Order ID"
            value={formData.order_id || ""}
            onChange={(e) =>
              setFormData({ ...formData, order_id: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Customer"
            value={formData.customer || ""}
            onChange={(e) =>
              setFormData({ ...formData, customer: e.target.value })
            }
          />
          <input
            style={styles.input}
            placeholder="Amount"
            type="number"
            value={formData.amount || ""}
            onChange={(e) =>
              setFormData({ ...formData, amount: parseInt(e.target.value) })
            }
          />
        </div>
        <div style={styles.formActions}>
          <button
            style={{ ...styles.button, ...styles.saveButton }}
            onClick={() => onSave(formData)}
          >
            <Save size={16} /> Save
          </button>
          <button
            style={{ ...styles.button, ...styles.cancelButton }}
            onClick={onCancel}
          >
            <X size={16} /> Cancel
          </button>
        </div>
      </div>
    );
  };

  const renderForm = () => {
    const commonProps = {
      data: editingItem,
      onSave: editingItem ? handleSave : handleAdd,
      onCancel: () => {
        setEditingItem(null);
        setShowAddForm(false);
      },
    };

    if (showAddForm || editingItem) {
      switch (activeTab) {
        case "capacity":
          return <CapacityForm {...commonProps} />;
        case "month":
          return <MonthForm {...commonProps} />;
        case "week":
          return <WeekForm {...commonProps} />;
        case "schedule":
          return <ScheduleForm {...commonProps} />;
        default:
          return null;
      }
    }
    return null;
  };

  const renderTable = () => {
    if (loading) {
      return (
        <div style={styles.noData}>
          <RefreshCw size={24} className="animate-spin" />
          <p style={{ marginTop: "10px" }}>Loading data...</p>
        </div>
      );
    }

    if (filteredData.length === 0) {
      return (
        <div style={styles.noData}>
          <p>No data found</p>
          <button
            style={{ ...styles.button, ...styles.addButton, marginTop: "20px" }}
            onClick={() => fetchData()}
          >
            <RefreshCw size={16} /> Refresh Data
          </button>
        </div>
      );
    }

    const headers = Object.keys(filteredData[0]).filter(
      (key) =>
        !key.startsWith("@") &&
        !key.startsWith("system:") &&
        !key.startsWith("relship:")
    );

    return (
      <div style={styles.tableContainer}>
        <table style={styles.table}>
          <thead>
            <tr style={styles.tableHeader}>
              {headers.map((header) => (
                <th key={header} style={styles.th}>
                  {header.replace(/_/g, " ").toUpperCase()}
                </th>
              ))}
              <th style={styles.th}>ACTIONS</th>
            </tr>
          </thead>
          <tbody>
            {filteredData.map((item, index) => (
              <tr
                key={item.cdb_object_id || item.order_id || index}
                style={{
                  ...styles.tableRow,
                  ":hover": { background: "rgba(59, 130, 246, 0.05)" },
                }}
                onMouseEnter={(e) =>
                  (e.target.parentElement.style.background =
                    "rgba(59, 130, 246, 0.05)")
                }
                onMouseLeave={(e) =>
                  (e.target.parentElement.style.background =
                    "rgba(255, 255, 255, 0.4)")
                }
              >
                {headers.map((header) => (
                  <td key={header} style={styles.td}>
                    {item[header]}
                  </td>
                ))}
                <td style={styles.td}>
                  <div style={styles.actionButtons}>
                    <button
                      style={{
                        ...styles.button,
                        ...styles.editButton,
                        opacity: loading ? 0.6 : 1,
                        cursor: loading ? "not-allowed" : "pointer",
                      }}
                      onClick={() => handleEdit(item)}
                      disabled={loading}
                    >
                      <Edit size={16} />
                    </button>
                    <button
                      style={{
                        ...styles.button,
                        ...styles.deleteButton,
                        opacity: loading ? 0.6 : 1,
                        cursor: loading ? "not-allowed" : "pointer",
                      }}
                      onClick={() =>
                        handleDelete(item.cdb_object_id || item.order_id)
                      }
                      disabled={loading}
                    >
                      <Trash2 size={16} />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  const styles = {
    container: {
      margin: "0 auto",
      padding: "20px",
      fontFamily: "'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
      background:
        "linear-gradient(135deg, #f8fafc 0%, #e2e8f0 50%, #f1f5f9 100%)",
      minHeight: "100vh",
      position: "relative",
    },
    card: {
      background: "rgba(255, 255, 255, 0.9)",
      borderRadius: "24px",
      padding: "40px",
      boxShadow:
        "0 25px 50px rgba(0, 0, 0, 0.08), 0 0 0 1px rgba(255, 255, 255, 0.5)",
      backdropFilter: "blur(20px)",
      border: "1px solid rgba(255, 255, 255, 0.2)",
      position: "relative",
      overflow: "hidden",
    },
    header: {
      textAlign: "center",
      color: "#1e293b",
      marginBottom: "40px",
      fontSize: "3em",
      fontWeight: "800",
      background:
        "linear-gradient(135deg, #3b82f6 0%, #8b5cf6 50%, #06b6d4 100%)",
      WebkitBackgroundClip: "text",
      WebkitTextFillColor: "transparent",
      backgroundClip: "text",
      letterSpacing: "-0.02em",
      textShadow: "0 0 30px rgba(59, 130, 246, 0.3)",
    },
    tabs: {
      display: "flex",
      marginBottom: "30px",
      background: "rgba(255, 255, 255, 0.6)",
      borderRadius: "20px",
      padding: "8px",
      boxShadow:
        "inset 0 2px 10px rgba(0, 0, 0, 0.05), 0 8px 25px rgba(0, 0, 0, 0.1)",
      backdropFilter: "blur(15px)",
      border: "1px solid rgba(255, 255, 255, 0.3)",
    },
    tab: {
      flex: 1,
      padding: "16px 24px",
      background: "transparent",
      border: "none",
      cursor: "pointer",
      fontSize: "15px",
      fontWeight: "600",
      color: "#64748b",
      borderRadius: "16px",
      transition: "all 0.4s cubic-bezier(0.4, 0, 0.2, 1)",
      position: "relative",
      overflow: "hidden",
    },
    activeTab: {
      background:
        "linear-gradient(135deg, #3b82f6 0%, #8b5cf6 50%, #06b6d4 100%)",
      color: "white",
      transform: "translateY(-1px)",
      boxShadow:
        "0 10px 25px rgba(59, 130, 246, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.3)",
      fontWeight: "700",
    },
    controls: {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      marginBottom: "30px",
      gap: "20px",
    },
    searchContainer: {
      position: "relative",
      flex: 1,
      maxWidth: "450px",
    },
    searchInput: {
      width: "100%",
      padding: "16px 50px 16px 20px",
      border: "2px solid rgba(59, 130, 246, 0.1)",
      borderRadius: "50px",
      fontSize: "15px",
      outline: "none",
      transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
      background: "rgba(255, 255, 255, 0.8)",
      backdropFilter: "blur(10px)",
      color: "#1e293b",
      fontWeight: "500",
      boxShadow: "0 4px 15px rgba(0, 0, 0, 0.05)",
    },
    searchIcon: {
      position: "absolute",
      right: "20px",
      top: "50%",
      transform: "translateY(-50%)",
      color: "#64748b",
      transition: "color 0.3s ease",
    },
    button: {
      display: "flex",
      alignItems: "center",
      gap: "10px",
      padding: "14px 28px",
      border: "none",
      borderRadius: "50px",
      cursor: "pointer",
      fontSize: "15px",
      fontWeight: "600",
      transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
      outline: "none",
      position: "relative",
      overflow: "hidden",
      textTransform: "uppercase",
      letterSpacing: "0.5px",
      boxShadow: "0 4px 15px rgba(0, 0, 0, 0.1)",
    },
    addButton: {
      background: "linear-gradient(135deg, #10b981 0%, #059669 100%)",
      color: "white",
      boxShadow: "0 8px 25px rgba(16, 185, 129, 0.3)",
    },
    editButton: {
      background: "linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%)",
      color: "white",
      padding: "10px 14px",
      marginRight: "8px",
      boxShadow: "0 4px 15px rgba(59, 130, 246, 0.3)",
    },
    deleteButton: {
      background: "linear-gradient(135deg, #ef4444 0%, #dc2626 100%)",
      color: "white",
      padding: "10px 14px",
      boxShadow: "0 4px 15px rgba(239, 68, 68, 0.3)",
    },
    saveButton: {
      background: "linear-gradient(135deg, #10b981 0%, #059669 100%)",
      color: "white",
      boxShadow: "0 8px 25px rgba(16, 185, 129, 0.3)",
    },
    cancelButton: {
      background: "linear-gradient(135deg, #6b7280 0%, #4b5563 100%)",
      color: "white",
      boxShadow: "0 8px 25px rgba(107, 114, 128, 0.3)",
    },
    tableContainer: {
      background: "rgba(255, 255, 255, 0.95)",
      borderRadius: "20px",
      overflow: "hidden",
      boxShadow:
        "0 20px 40px rgba(0, 0, 0, 0.06), inset 0 1px 0 rgba(255, 255, 255, 0.7)",
      overflowX: "auto",
      backdropFilter: "blur(20px)",
      border: "1px solid rgba(255, 255, 255, 0.2)",
    },
    table: {
      width: "100%",
      borderCollapse: "collapse",
      fontSize: "14px",
      fontWeight: "500",
    },
    tableHeader: {
      background:
        "linear-gradient(135deg, #1e293b 0%, #334155 50%, #475569 100%)",
      color: "white",
      position: "relative",
    },
    th: {
      padding: "18px 16px",
      textAlign: "left",
      fontWeight: "700",
      fontSize: "13px",
      letterSpacing: "1px",
      whiteSpace: "nowrap",
      textTransform: "uppercase",
      position: "relative",
    },
    tableRow: {
      borderBottom: "1px solid rgba(226, 232, 240, 0.5)",
      transition: "all 0.2s ease",
      background: "rgba(255, 255, 255, 0.4)",
    },
    td: {
      padding: "16px",
      whiteSpace: "nowrap",
      overflow: "hidden",
      textOverflow: "ellipsis",
      maxWidth: "180px",
      color: "#334155",
      fontWeight: "500",
      fontSize: "14px",
    },
    actionButtons: {
      display: "flex",
      gap: "5px",
    },
    formContainer: {
      background: "rgba(255, 255, 255, 0.9)",
      borderRadius: "20px",
      padding: "35px",
      marginBottom: "30px",
      boxShadow:
        "0 25px 50px rgba(0, 0, 0, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.7)",
      backdropFilter: "blur(20px)",
      border: "1px solid rgba(255, 255, 255, 0.3)",
      position: "relative",
      overflow: "hidden",
    },
    formTitle: {
      color: "#1e293b",
      marginBottom: "30px",
      fontSize: "1.8em",
      fontWeight: "700",
      textAlign: "center",
      background: "linear-gradient(135deg, #3b82f6 0%, #8b5cf6 100%)",
      WebkitBackgroundClip: "text",
      WebkitTextFillColor: "transparent",
      backgroundClip: "text",
    },
    formGrid: {
      display: "grid",
      gridTemplateColumns: "repeat(auto-fit, minmax(280px, 1fr))",
      gap: "20px",
      marginBottom: "30px",
    },
    input: {
      padding: "16px 20px",
      border: "2px solid rgba(59, 130, 246, 0.1)",
      borderRadius: "12px",
      fontSize: "15px",
      outline: "none",
      transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
      background: "rgba(255, 255, 255, 0.8)",
      color: "#1e293b",
      fontWeight: "500",
      backdropFilter: "blur(10px)",
      boxShadow: "0 4px 15px rgba(0, 0, 0, 0.05)",
    },
    formActions: {
      display: "flex",
      justifyContent: "center",
      gap: "20px",
      marginTop: "10px",
    },
    noData: {
      textAlign: "center",
      padding: "60px",
      color: "#64748b",
      fontSize: "18px",
      fontWeight: "500",
      background: "rgba(255, 255, 255, 0.6)",
      borderRadius: "20px",
      backdropFilter: "blur(10px)",
    },
  };

  return (
    <div style={styles.container}>
      <div style={styles.card}>
        {/* Tabs */}
        <div style={styles.tabs}>
          {[
            { key: "capacity", label: "Capacity Master" },
            { key: "month", label: "Monthly Production Plan" },
            { key: "week", label: "Daily Production Plan" },
            { key: "schedule", label: "Sales Plan" },
          ].map((tab) => (
            <button
              key={tab.key}
              style={{
                ...styles.tab,
                ...(activeTab === tab.key ? styles.activeTab : {}),
              }}
              onClick={() => setActiveTab(tab.key)}
            >
              {tab.label}
            </button>
          ))}
        </div>

        {/* Controls */}
        <div style={styles.controls}>
          <div style={styles.searchContainer}>
            <input
              type="text"
              placeholder="🔍 Search across all data..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              style={{
                ...styles.searchInput,
                ":focus": {
                  borderColor: "#3b82f6",
                  transform: "translateY(-1px)",
                  boxShadow: "0 10px 40px rgba(59, 130, 246, 0.15)",
                },
              }}
              onFocus={(e) => {
                e.target.style.borderColor = "#3b82f6";
                e.target.style.transform = "translateY(-1px)";
                e.target.style.boxShadow =
                  "0 10px 40px rgba(59, 130, 246, 0.15)";
                e.target.nextElementSibling.style.color = "#3b82f6";
              }}
              onBlur={(e) => {
                e.target.style.borderColor = "rgba(59, 130, 246, 0.1)";
                e.target.style.transform = "translateY(0)";
                e.target.style.boxShadow = "0 4px 15px rgba(0, 0, 0, 0.05)";
                e.target.nextElementSibling.style.color = "#64748b";
              }}
            />
            <Search size={20} style={styles.searchIcon} />
          </div>

          <button
            style={{
              ...styles.button,
              ...styles.addButton,
              ":hover": {
                transform: "translateY(-2px)",
                boxShadow: "0 12px 35px rgba(16, 185, 129, 0.4)",
              },
              opacity: loading ? 0.6 : 1,
              cursor: loading ? "not-allowed" : "pointer",
            }}
            onClick={() => setShowAddForm(true)}
            disabled={loading}
            onMouseEnter={(e) => {
              if (!loading) {
                e.target.style.transform = "translateY(-2px)";
                e.target.style.boxShadow =
                  "0 12px 35px rgba(16, 185, 129, 0.4)";
              }
            }}
            onMouseLeave={(e) => {
              if (!loading) {
                e.target.style.transform = "translateY(0)";
                e.target.style.boxShadow = "0 8px 25px rgba(16, 185, 129, 0.3)";
              }
            }}
          >
            <Plus size={16} />
            {loading ? "Loading..." : "Add New"}
          </button>
        </div>

        {/* Form */}
        {renderForm()}

        {/* Table */}
        {renderTable()}
      </div>
    </div>
  );
};

export default MasterDataInterface;
